@use "base/mixins" as m;
@use "base/variables" as v;

.myaccount_page {
    @include m.pt230px;
    @include m.pblock160(true);
    min-height: 100vh;

    &-container {
        @include m.container_side;
    }

    &-title {
        h1 {
            @include m.fz60;
            font-weight: 400;
        }

        p {
            @include m.p;
            opacity: 0.9;
            text-align: center;
            @include m.mt40_for_actions(false);
        }
    }

    .woocommerce {
        @include m.delete_pseudo;

        &-notices-wrapper {

            p,
            li,
            span {
                @include m.p2(v.$black);
            }
        }

        &-grid {
            width: 100%;

            @media (min-width: v.$tv) {
                display: grid;
                grid-template-columns: m.px-to-vw(375px) 1fr;
                grid-gap: m.px-to-vw(20px);
            }

            @include m.tv {}
        }
    }
}

.woocommerce_form_custom {
    @include m.delete_pseudo;

    @media (min-width: v.$tv) {
        max-width: m.px-to-vw(638px);
    }

    @include m.tv {
        max-width: 500px;
    }

    margin-inline: auto;

    &>* {
        @include m.form-row;
    }

    .woocommerce-ResetPassword, .woocommerce-form {
        @include m.form-row;
        border: none !important;

        &-fields {
            @include m.mt40_for_actions(false);

            @media (min-width: v.$tv) {
                @include m.flex(center, m.px-to-vw(32px));
                flex-direction: column;
            }

            @include m.tv {
                @include m.flex(center, 24px);
                flex-direction: column;
            }
        }

        .form-row {
            @include m.form-row;

            .required {
                display: none !important;
            }

            span {
                display: block;
                width: 100%;
            }

            .lost_password {
                font-family: v.$gotham_light;
                font-weight: 500;
                color: v.$black;
                text-decoration: underline;
                opacity: 0.6;
                display: block;

                @media (min-width: v.$tv) {
                    font-size: m.px-to-vw(14px);
                    margin-top: m.px-to-vw(24px);
                    letter-spacing: m.px-to-vw(1.4px);
                }

                @include m.tv {
                    font-size: 12px;
                    margin-top: 16px;
                    letter-spacing: 0.3px;
                }
            }

            .woocommerce-password {
                &-strength {
                    padding: 0 !important;
                    @include m.p2;

                    @media (min-width: v.$tv) {
                        height: m.px-to-vw(32px);
                    }

                    @include m.tv {
                        height: 24px;
                    }

                    @include m.flex(center);
                }

                &-hint {
                    font-family: v.$gotham_light;
                    font-weight: 300;
                    line-height: 1.2em;

                    @media (min-width: v.$tv) {
                        margin-top: m.px-to-vw(8px) !important;
                        font-size: m.px-to-vw(13px) !important;
                    }

                    @include m.tv {
                        font-size: 11px !important;
                    }
                }
            }
        }

        .woocommerce-privacy-policy-text {
            display: none !important;
        }

        .show-password-input {
            &::before {
                @media (min-width: v.$tv) {
                    @include m.square(m.px-to-vw(20px));
                }

                @include m.tv {}
            }
        }

        label {
            font-family: v.$gotham_m !important;
            font-weight: 400 !important;
            color: v.$black !important;
            opacity: 0.6;
            display: block;
            line-height: normal !important;

            @media (min-width: v.$tv) {
                margin-bottom: m.px-to-vw(12px) !important;
                letter-spacing: m.px-to-vw(1.4px) !important;
                font-size: m.px-to-vw(14px) !important;
            }

            @include m.tv {
                margin-bottom: 6.8px !important;
                letter-spacing: 0.2px !important;
                font-size: 11.8px !important;
            }
        }

        input:not([type="checkbox"]) {
            color: v.$black !important;
            border-radius: 0 !important;
            font-family: v.$gotham_light !important;
            font-weight: 300 !important;

            @media (min-width: v.$tv) {
                padding: 0 m.px-to-vw(16px) !important;
                height: m.px-to-vw(52px) !important;
                font-size: m.px-to-vw(17px) !important;
                line-height: m.px-to-vw(24px) !important;
                letter-spacing: m.px-to-vw(1.7px) !important;
                border: m.px-to-vw(1px) solid v.$light_brown !important;
            }

            @include m.tv {
                padding: 0 12px !important;
                height: 40px !important;
                font-size: 14px !important;
                line-height: 1.2em !important;
                letter-spacing: 0.4px !important;
                border: 1px solid v.$light_brown !important;
            }
        }

        [type="submit"] {
            @include m.form-row;
            background: v.$light_brown !important;
            border-radius: 0 !important;
            text-transform: uppercase;
            color: v.$white !important;
            font-family: v.$gotham_light !important;
            font-weight: 300 !important;

            @media (min-width: v.$tv) {
                letter-spacing: m.px-to-vw(1.7px) !important;
                @include m.flex(center, m.px-to-vw(16px));
                line-height: normal !important;
                height: m.px-to-vw(52px) !important;
                font-size: m.px-to-vw(17px) !important;
            }

            @include m.tv {
                letter-spacing: 0.4px !important;
                @include m.flex(center, 10px);
                height: 40px !important;
                font-size: 14px !important;
                line-height: 1.2em !important;
            }

            svg {
                @media (min-width: v.$tv) {
                    width: m.px-to-vw(24px);
                }

                object-fit: contain;
                object-position: center;
            }
        }

        .form-action {
            width: 100%;

            @media (min-width: v.$tv) {
                margin-top: m.px-to-vw(48px);
            }

            @include m.tv {
                margin-top: 36px;
            }

            a {
                display: block;
                width: fit-content;
                margin-inline: auto;
                @include m.p(v.$light_brown, v.$gotham_m);

                @media (min-width: v.$tv) {
                    margin-top: m.px-to-vw(32px);
                }

                @include m.tv {
                    margin-top: 24px;
                }

                @include m.tablet {
                    margin-top: 20px;
                }
            }
        }
    }

    &[data-scene="1"] {
        .u-column2 {
            display: none;
        }
    }

    &[data-scene="2"] {
        .u-column1 {
            display: none;
        }
    }

}

.woocommerce-MyAccount {

    &-navigation,
    &-content {
        @include m.form-row;
    }

    &-navigation {
        @media (min-width: v.$tv) {
            padding-top: m.px-to-vw(16px);
        }

        @include m.tv {
            padding-top: 10px;
        }

        ul,
        ol {
            list-style: none;
        }

        &-link {
            width: 100%;

            a {
                width: 100%;

                @media (min-width: v.$tv) {
                    @include m.flex(flex-start, m.px-to-vw(16px));
                    padding: m.px-to-vw(20px) m.px-to-vw(21px);
                }

                @include m.tv {
                    @include m.flex(flex-start, 10px);
                    padding: 14px 15px;
                }
            }
        }
    }

    &-content {}

    &-header_profile {
        @media (min-width: v.$tv){
            margin-bottom: m.px-to-vw(52px);
        }
        @include m.tv{
            margin-bottom: 38px;
        }

        h1 {
            @include m.fz60;
            text-align: left;
            font-weight: 300;
            @include m.mb96;
        }

        h2 {
            @include m.h4v2(v.$black, v.$cormorant, false);
            font-weight: 300;

            @media (min-width: v.$tv) {
                letter-spacing: m.px-to-vw(-1.2px);
            }

            @include m.tv {
                letter-spacing: -0.4px;
            }
        }

        a {
            display: block;
            background: v.$light_brown;
            color: v.$white;
            text-transform: uppercase;
            font-family: v.$gotham_m;
            font-weight: 300;
            @media (min-width: v.$tv) {
                letter-spacing: m.px-to-vw(0.8px);
                @include m.flex(flex-start, m.px-to-vw(16px));
                padding: m.px-to-vw(20px) m.px-to-vw(21px);
            }
            
            @include m.tv {
                letter-spacing: 0.05px;
                @include m.flex(flex-start, 10px);
                padding: 14px 15px;
            }
        }

        div {
            @include m.flex(space-between);
            @media (min-width: v.$tv){
                padding-right: m.px-to-vw(48px);
            }
            @include m.tv{
                padding-right: 36px;
            }
            @include m.desktop{
                padding-right: 0;
            }
        }
    }
}